import "./userInfo.css";
import '../../desktop.css';
import UserImg from "../../images/image-jeremy.png";

const UserInfo = ({ clickHandler, active }) => {
  return (
    <div className="user-card">
      <div className="user-info">
        <div className="profile-picture">
          <img src={UserImg} alt="User profile" />
        </div>
        <div className="card-text">
          <h3 className="card-assigner">Report for</h3>
          <h1 className="user-name">Jeremy Robson</h1>
        </div>
      </div>

      <ul className="time-nav">
        <li>
          <button
            className={`nav-link ${active === "daily" ? "active" : ""}`}
            onClick={clickHandler}
            id="daily"
          >
            Daily
          </button>
        </li>
        <li>
          <button
            className={`nav-link ${active === "weekly" ? "active" : ""}`}
            onClick={clickHandler}
            id="weekly"
          >
            Weekly
          </button>
        </li>
        <li>
          <button
            className={`nav-link ${active === "monthly" ? "active" : ""}`}
            onClick={clickHandler}
            id="monthly"
          >
            Monthly
          </button>
        </li>
      </ul>
    </div>
  );
};

export default UserInfo;
