import React, { useState } from "react";
import UserInfo from "../userInfo/UserInfo";
import Cards from "../card/Cards";
import "./styles.css";
import "../../desktop.css";
import Data from "../../data.json";

const Wrapper = () => {
  const [active, setActive] = useState("daily");

  const handleClick = (e) => {
    setActive(e.target.id);
  };

  return (
    <div className="main">
      <div className="user-wrapper">
        <UserInfo active={active} clickHandler={handleClick} />
      </div>
      <div className="data-wrapper">
        {Data.length > 0
          ? Data.map((stats, index) => {
              return (
                <Cards
                  active={active}
                  category={stats.title}
                  title={stats.title}
                  current={
                    active === "daily"
                      ? stats.timeframes.daily.current
                      : active === "weekly"
                      ? stats.timeframes.weekly.current
                      : stats.timeframes.monthly.current
                  }
                  previous={
                    active === "daily"
                      ? stats.timeframes.daily.previous
                      : active === "weekly"
                      ? stats.timeframes.weekly.previous
                      : stats.timeframes.monthly.current
                  }
                  key={index}
                />
              );
            })
          : "No data to show for this user."}
      </div>
    </div>
  );
};

export default Wrapper;
